<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">

        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="createParty('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="createParty('stay')">
                  <span>{{ $t('SUBMIT_STAY') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="createParty('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="createParty('enroll')" v-if="partyTypeEnumId === 'PtyPerson'">
                  <span>{{ $t('SUBMIT_ENROLL') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('TYPE')" icon="far fa-tag">
          <div class="flex justify-start mb-4">
            <ul class="leftx">
              <li v-for="(type, index) in partyTypes" :key="index">
                <vs-radio v-model="partyTypeEnumId" :vs-value="type.enumId"><i :class="[partyTypeIcon(type), 'w-6 text-center']"></i> {{ type.description }}</vs-radio>
              </li>
            </ul>
          </div>
        </tab-content>

        <tab-content :title="$t('PROFILE')" icon="far fa-file-alt" :before-change="loadCustomer">
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PSEUDO_ID') }}</label>
              <vs-input class="w-full" v-model="pseudoId"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('EXTERNAL_ID') }}</label>
              <vs-input class="w-full" v-model="externalId"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('COMMENTS') }}</label>
              <vs-textarea class="w-full" v-model="comments"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <vs-checkbox v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('DETAILS')" icon="far fa-user" :before-change="loadCustomer">
          <div v-if="partyTypeEnumId === 'PtyPerson'">
            <div class="nv-row mb-4">
              <div class="nv-col sm:w w-full">
                <div class="flex justify-end">
                  <span><i class="far fa-male fa-lg"></i></span>
                  <vs-switch class="mx-2" v-model="genderSwitch" color="#FF69B4"/>
                  <span><i class="far fa-female fa-lg"></i></span>
                </div>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('SALUTATION') }}</label>
                <vs-input class="w-full" v-model="salutation"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full required">
                <label class="vs-input--label">{{ $t('FIRST_NAME') }}</label>
                <vs-input class="w-full" v-model="firstName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('MIDDLE_NAME') }}</label>
                <vs-input class="w-full" v-model="middleName"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full required">
                <label class="vs-input--label">{{ $t('LAST_NAME') }}</label>
                <vs-input class="w-full" v-model="lastName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('SUFFIX') }}</label>
                <vs-input class="w-full" v-model="suffix"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('MAIDEN_NAME') }}</label>
                <vs-input class="w-full" v-model="mothersMaidenName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('BIRTH_DATE') }}</label>
                <nv-input-group>
                  <template slot="append">
                    <div class="append-text bg-dark">
                      <i class="far fa-calendar-alt"></i>
                    </div>
                  </template>
                  <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="birthDate" :format="dateFormat"></datepicker>
                </nv-input-group>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('DECEASED_DATE') }}</label>
                <nv-input-group>
                  <template slot="append">
                    <div class="append-text bg-dark">
                      <i class="far fa-calendar-alt"></i>
                    </div>
                  </template>
                  <datepicker class="w-full" :placeholder="$t('SELECT_DATE')" v-model="deceasedDate" :format="dateFormat"></datepicker>
                </nv-input-group>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('MARITAL_STATUS') }}</label>
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="maritalStatusEnumId">
                  <vs-select-item v-for="(enumeration, index) in maritalStatuses" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
                </vs-select>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('RESIDENCE_STATUS') }}</label>
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="residenceStatusEnumId">
                  <vs-select-item v-for="(enumeration, index) in residenceStatuses" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
                </vs-select>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('EMPLOYMENT_STATUS') }}</label>
                <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="employmentStatusEnumId">
                  <vs-select-item v-for="(enumeration, index) in employmentStatuses" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
                </vs-select>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('OCCUPATION') }}</label>
                <vs-input class="w-full" v-model="occupation"/>
              </div>
            </div>
          </div>
          <div v-if="partyTypeEnumId === 'PtyOrganization'">
            <div class="nv-row mb-4">
              <div class="nv-col sm:w w-full required">
                <label class="vs-input--label">{{ $t('ORGANIZATION_NAME') }}</label>
                <vs-input class="w-full" v-model="organizationName"/>
              </div>
            </div>
            <div class="nv-row mb-4">
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('NUMBER_OF_EMPLOYEES') }}</label>
                <vs-input type="number" class="w-full" v-model="numEmployees"/>
              </div>
              <div class="nv-col sm:w-1/2 w-full">
                <label class="vs-input--label">{{ $t('ANNUAL_REVENUE') }}</label>
                <vs-input type="number" class="w-full" v-model="annualRevenue"/>
              </div>
            </div>
          </div>
          <div v-if="partyTypeEnumId === 'PtyAutomatedAgent'">
            <vs-alert active="true" color="warning" class="mb-5">
              <p>{{ $t('NOTHING_MORE') }}</p>
            </vs-alert>
          </div>
        </tab-content>

        <tab-content :title="$t('CONTACTS')" icon="far fa-phone">
          <div id="contact-loading-container" class="vs-con-loading__container">
            <div class="inline-form p-4 mb-base" v-if="contactMechFormVisible">
              <div class="nv-row mb-4">
                <div class="nv-col sm:w w-full">
                  <div class="flex justify-end">
                    <span><i class="far fa-phone"></i></span>
                    <vs-switch class="mx-2" v-model="contactMechEmail"/>
                    <span><i class="far fa-at"></i></span>
                  </div>
                </div>
              </div>
              <div class="nv-row mb-4">
                <div class="nv-col sm:w-1/2 w-full required" v-if="!contactMechEmail">
                  <label>{{ $t('PHONE_NUMBER') }}</label>
                  <div>
                    <div class="float-left mx-2 pt-2">+</div>
                    <vs-input class="float-left w-16" v-model="countryCode" :title="$t('COUNTRY_CODE')"/>
                    <span class="float-left mx-2 pt-2">-</span>
                    <vs-input class="float-left w-16" v-model="areaCode" :title="$t('AREA_CODE')"/>
                    <span class="float-left mx-2 pt-2">-</span>
                    <vs-input class="float-left w-100" v-model="contactNumber" :title="$t('PHONE_NUMBER')"/>
                    <span class="float-left mx-2 pt-2">x</span>
                    <vs-input class="float-left w-16" v-model="extension" :title="$t('EXTENSION')"/>
                    <div class="clearfix"></div>
                  </div>
                </div>
                <div class="nv-col sm:w-1/2 w-full required" v-if="contactMechEmail">
                  <label>{{ $t('EMAIL_ADDRESS') }}</label>
                  <vs-input class="w-full" type="email" v-model="infoString" :title="$t('EMAIL_ADDRESS')"/>
                </div>
                <div class="nv-col sm:w-1/2 w-full required">
                  <label>{{ $t('PURPOSE') }}</label>
                  <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="contactMechPurposeId">
                    <vs-select-item v-for="(purpose, index) in contactMechPurposes" :key="index" :value="purpose.contactMechPurposeId" :text="purpose.description"/>
                  </vs-select>
                </div>
              </div>
              <div class="nv-row mb-4" v-if="!contactMechEmail">
                <div class="nv-col w-full">
                  <label>{{ $t('ASK_FOR_NAME') }}</label>
                  <vs-input class="w-full" v-model="askForName" :title="$t('ASK_FOR')"/>
                </div>
              </div>
              <div class="nv-row">
                <div class="nv-col sm:w-1/2 w-full">
                  <div class="flex justify-left">
                    <vs-button class="px-5 py-3 mr-2" @click="createPartyContactMech" color="primary" type="filled">{{ $t('SUBMIT') }}</vs-button>
                    <vs-button class="px-5 py-3 mr-2" @click="hideContactMechForm" color="primary" type="border">{{ $t('CANCEL') }}</vs-button>
                  </div>
                </div>
              </div>
            </div>

            <vs-table class="mb-base" :data="contactMechTableData" :noDataText="contactMechTableNoDataText" :sst="true">
              <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
                <div class="flex flex-wrap-reverse items-center">
                  <vs-button class="p-3 mr-4" @click="showContactMechForm" :disabled="contactMechFormVisible" color="primary" type="filled" icon="fa-plus" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('ADD') }}</vs-button>
                </div>
              </div>

              <template slot="thead">
                <vs-th sort-key="contactMechTypeDescription">
                  {{ this.$t('TYPE') }}
                </vs-th>
                <vs-th sort-key="details">
                  {{ this.$t('DETAILS') }}
                </vs-th>
                <vs-th sort-key="contactMechPurposeDescription">
                  {{ this.$t('PURPOSE') }}
                </vs-th>
                <vs-th>
                  {{ this.$t('OPTIONS') }}
                </vs-th>
              </template>

              <template slot-scope="{data}">
                <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
                  <vs-td :data="data[index].contactMechTypeDescription">
                    {{ data[index].contactMechTypeDescription }}
                  </vs-td>
                  <vs-td :data="data[index].details">
                    {{ contactMechDetails(data[index]) | placeholder('-') }}
                  </vs-td>
                  <vs-td :data="data[index].contactMechPurposeDescription">
                    {{ data[index].contactMechPurposeDescription }}
                  </vs-td>
                  <vs-td :data="data[index].options">
                    <vs-button class="p-3 mr-4" color="danger" @click="removePartyContactMech(index)">{{ $t('DELETE') }}</vs-button>
                  </vs-td>
                </vs-tr>
              </template>
            </vs-table>
          </div>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import PartyService from '@/services/nucleus/PartyService'
import ContactMechService from '@/services/nucleus/ContactMechService'
import EnumerationService from '@/services/commons/EnumerationService'
import { FormWizard, TabContent } from 'vue-form-wizard'
import Datepicker from 'vuejs-datepicker'

export default {
  name: 'party-create',
  mixins: [AlertMixin, DatePickerMixin, LoadingMixin, ListLayoutMixin],
  components: {
    FormWizard,
    TabContent,
    Datepicker
  },
  data: function () {
    return {
      partyTypes: [],
      partyTypeEnumId: 'PtyPerson',
      pseudoId: '',
      externalId: '',
      comments: '',
      disabled: false,
      salutation: '',
      firstName: '',
      middleName: '',
      lastName: '',
      suffix: '',
      mothersMaidenName: '',
      birthDate: '',
      deceasedDate: '',
      maritalStatuses: [],
      maritalStatusEnumId: 'MarsSingle',
      employmentStatuses: [],
      employmentStatusEnumId: 'EmpsFullTime',
      residenceStatuses: [],
      residenceStatusEnumId: 'RessOwn',
      occupation: '',
      organizationName: '',
      annualRevenue: 0,
      numEmployees: 0,
      gender: 'M',
      genderSwitch: false,
      contactMechTableData: [],
      contactMechTableFilter: null,
      contactMechTableFilterTimer: null,
      contactMechTableOrderBy: null,
      contactMechTableContainer: 'contact-loading-container',
      contactMechTableBusy: false,
      contactMechTableTotalRows: 0,
      contactMechTableCurrentPage: 1,
      contactMechTablePerPage: 10,
      contactMechFormVisible: false,
      infoString: '',
      countryCode: '',
      areaCode: '',
      contactNumber: '',
      extension: '',
      contactMechPurposes: [],
      contactMechPurposeId: 'PhonePrimary',
      contactMechTypeEnumId: 'CmtTelecomNumber',
      contactMechEmail: false,
      askForName: ''
    }
  },
  computed: {
    contactMechTableNoDataText: function () {
      return this.contactMechTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    contactMechTableTotalPages: function () {
      return Math.ceil(this.contactMechTableTotalRows / this.contactMechTablePerPage)
    },
    contactMechTablePagingInfo: function () {
      return this.pagingInfo(this.contactMechTableCurrentPage, this.contactMechTablePerPage, this.contactMechTableTotalRows)
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'PartyType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.partyTypes = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'MaritalStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.maritalStatuses = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'ResidenceStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.residenceStatuses = response.data.enumerationList || []
      })

      EnumerationService.enumerations({
        enumTypeId: 'EmploymentStatus',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.employmentStatuses = response.data.enumerationList || []
      })
    },
    loadCustomer () {
      if (this.externalId !== '') {
        let self = this
        this.startLoading('loading-container')
        PartyService.customer({
          customerId: this.externalId
        }).then(function (response) {
          self.stopLoading('loading-container')
          let customer = response.data.customer || {}
          self.firstName = customer.firstName || ''
          self.lastName = customer.lastName || ''
          self.organizationName = (self.firstName + ' ' + self.lastName).trim()
        }).catch(function (error) {
          self.stopLoading('loading-container')
          self.showDangerAlert(error.response.data.errors)
        })
      }
      return true
    },
    partyTypeIcon (partyType) {
      if (partyType.enumId === 'PtyOrganization') {
        return 'fal fa-university'
      } else if (partyType.enumId === 'PtyPerson') {
        return 'fal fa-user'
      } else if (partyType.enumId === 'PtyAutomatedAgent') {
        return 'fal fa-robot'
      } else {
        return ''
      }
    },
    showContactMechForm () {
      this.contactMechFormVisible = true
    },
    hideContactMechForm () {
      this.contactMechFormVisible = false
    },
    contactMechDetails (item) {
      if (item.contactMechTypeEnumId === 'CmtTelecomNumber') {
        let phoneNumber = '+'
        phoneNumber += item.countryCode ? item.countryCode : ''
        phoneNumber += item.areaCode ? '-' + item.areaCode : ''
        phoneNumber += item.contactNumber ? '-' + item.contactNumber : ''
        phoneNumber += item.extension ? 'x' + item.extension : ''
        return phoneNumber
      } else {
        return item.infoString
      }
    },
    createPartyContactMech () {
      this.contactMechTableData.push({
        infoString: this.infoString,
        countryCode: this.countryCode,
        areaCode: this.areaCode,
        contactNumber: this.contactNumber,
        extension: this.extension,
        contactMechTypeEnumId: this.contactMechTypeEnumId,
        contactMechTypeDescription: this.contactMechTypeEnumId === 'CmtTelecomNumber' ? 'Phone' : 'Email',
        contactMechPurposeId: this.contactMechPurposeId,
        contactMechPurposeDescription: this.contactMechPurposes.find(this.findByContactMechPurposeId).description,
        askForName: this.askForName
      })
    },
    findByContactMechPurposeId (contactMechPurpose) {
      return contactMechPurpose.contactMechPurposeId === this.contactMechPurposeId
    },
    removePartyContactMech (index) {
      this.contactMechTableData.splice(index, 1)
    },
    loadContactPurposes () {
      let self = this
      ContactMechService.contactMechPurposes({
        contactMechTypeEnumId: this.contactMechTypeEnumId,
        orderByField: 'description',
        pageNoLimit: true
      }).then(function (response) {
        self.contactMechPurposes = response.data || []
        self.contactMechPurposeId = self.contactMechTypeEnumId === 'CmtTelecomNumber' ? 'PhonePrimary' : 'EmailPrimary'
      })
    },
    toggleMechType (mechType) {
      if (mechType === 'PHONE') {
        this.contactMechTypeEnumId = 'CmtTelecomNumber'
      } else if (mechType === 'EMAIL') {
        this.contactMechTypeEnumId = 'CmtEmailAddress'
      }
    },
    createParty (nextAction) {
      let self = this
      this.startLoading('loading-container')
      PartyService.createParty({
        pseudoId: this.pseudoId,
        partyTypeEnumId: this.partyTypeEnumId,
        externalId: this.externalId,
        comments: this.comments,
        disabled: this.disabled ? 'Y' : 'N',
        gender: this.gender,
        salutation: this.salutation,
        firstName: this.firstName,
        middleName: this.middleName,
        lastName: this.lastName,
        suffix: this.suffix,
        mothersMaidenName: this.mothersMaidenName,
        birthDate: this.birthDate,
        deceasedDate: this.deceasedDate,
        maritalStatusEnumId: this.maritalStatusEnumId,
        residenceStatusEnumId: this.residenceStatusEnumId,
        employmentStatusEnumId: this.employmentStatusEnumId,
        occupation: this.occupation,
        organizationName: this.organizationName,
        numEmployees: this.numEmployees,
        annualRevenue: this.annualRevenue,
        contactMechList: JSON.stringify(this.contactMechTableData)
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewParty', params: { id: response.data.partyId } })
          } else if (nextAction === 'enroll') {
            self.$router.push({ name: 'EnrollParty', params: { id: response.data.partyId } })
          } else {
            self.$router.push({ name: 'BrowseParty' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    toggleGenderType (gender) {
      this.gender = gender
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
    this.loadContactPurposes()
  },
  watch: {
    genderSwitch: function () {
      this.gender = this.genderSwitch ? 'F' : 'M'
    },
    contactMechEmail: function () {
      this.contactMechTypeEnumId = this.contactMechEmail ? 'CmtEmailAddress' : 'CmtTelecomNumber'
      this.loadContactPurposes()
    }
  }
}
</script>

<style>
  .con-vs-dropdown--menu { min-width: 130px; }
</style>
